<template>
  <div class="app">
    <main>
      <div class="search-box">
        <input 
          type="text" 
          class="search-bar" 
          placeholder="Search..."
          v-model="query"
          @keypress="fetchWeather"
        />
      </div>

      <div class="greeting" v-if="typeof weather.main == 'undefined'">
        <h1 class="title">Welcome To WeatherBuddy</h1>
        <p class="intro">WeatherBuddy is a easy to use web app that gives you the current weather of any city or town anywhere across the world in an instant. WeatherBuddy was built using Vue.js and the openweathermap api.</p>
        <p class="intro p2">To start, enter the city or town you would like information from, and press the enter key.</p>
      </div>

      <div class="gitlogo" v-if="typeof weather.main == 'undefined'">
        <a href="https://github.com/ColeHodge"><img class="githubLogo" src="./assets/GitHub-Mark-Light-120px-plus.png" alt=""></a>
      </div> 

      <div class="weather-wrap" v-if="typeof weather.main != 'undefined'">
        <div class="location-box">
          <div class="location">{{ weather.name }}, {{ weather.sys.country }}</div>
          <div class="date" >{{ dateBuidler() }}</div>
        </div>

        <div class="weather-box">
          <div class="temp">{{ Math.round(weather.main.temp) }}Â°c</div>
          <div class="weather">{{ weather.weather[0].main }}</div>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  name: 'App',
  components: {
  },

  data () {
    return {
      api_key: '189ac38e8246288b274cd25854f386e8',
      url_base: 'https://api.openweathermap.org/data/2.5/',
      query: '',
      weather: {},
    }
  },

  methods: {
    fetchWeather(e) {
      if (e.key == 'Enter') {
        fetch(`${this.url_base}weather?q=${this.query}&units=metric&appid=${this.api_key}`)
          .then(res => {
            return res.json();
          }).then(this.setResults);
      }
    },

    setResults(results) {
      this.weather = results;
    },

    dateBuidler() {
      let d = new Date();
      let months = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];
      let days = ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"];

      let day = days[d.getDay()];
      let date = d.getDate();
      let month = months[d.getMonth()];
      let year = d.getFullYear();

      let formatedDate =  `${day} ${date} ${month} ${year}`;
      return formatedDate
    },
  }

}
</script>


